document.addEventListener('DOMContentLoaded', function(){
    crearGaleria();
});
function crearGaleria(){
    const galeria = document.querySelector('.galeria-img');

    for(let i = 1; i <= 12; i++){
        const imagen = document.createElement('IMG');
        imagen.src = `build/img/thumb/${i}.webp`;
        imagen.dataset.imgId = i;

        //AÑADIR LA FUNCION DE MOSTRAR IMG
        imagen.onclick = mostrarImg;

        const lista = document.createElement('LI');
        lista.appendChild(imagen);
        
        galeria.appendChild(lista);
    }
}
function mostrarImg (e){
    

    const id = parseInt (e.target.dataset.imgId);
    //GENERAR LA IMAGEN

    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.webp`;
    imagen.classList.add('img-galeria');

    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');

    // TMB CUANDO SE CLICKEA SE CIERRA IMG
    overlay.onclick = function(){
        overlay.remove();
    }


    //BOTÓN PARA CERRAR IMG
    const cerrarImg = document.createElement('P');
    cerrarImg.textContent = 'X';
    cerrarImg.classList.add('btn-cerrar');

    //CUANDO SE PRESIONA, SE CIERRA LA IMG
    cerrarImg.onclick = function(){
        overlay.remove();
    }

    overlay.appendChild(cerrarImg);


    //Mostrar en el HTML
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
    

}